  .stars {
    display: flex;

    &__star {
      background-repeat: no-repeat;
      background-position: center;
      background-image: url(../images/star.svg);
      width: 16px;
      height: 16px;
    }

    &__star--4 {
      background-position: center;
      background-image: url(../images/star-active.svg) ;
    }

    &__star:not(:last-child) {
      margin-right: 4px;
    }
  }
