html {
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

body, h2, p, ul, li {
  margin: 0;
  padding: 0;
}

.container__nav {
  width: 100vh;
}

.nav {
  align-items: center;
  margin-right: 50px;
  padding-left: 0;
  display: flex;
}

.nav__link {
  color: #060b35;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition-duration: .3s;
  display: inline-block;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__list {
  align-items: center;
  margin-top: 0;
  margin-bottom: 0;
  padding-inline-start: 0;
  list-style: none;
  display: flex;
}

.nav__link:hover[data-qa="nav-hover"], .nav__link:hover {
  color: #00acdc;
  transition-delay: .3s;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  display: flex;
}

.img {
  width: 40px;
  height: 40px;
  padding: 10px 50px;
  display: block;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  grid-auto-flow: row;
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  padding: 32px 16px 16px;
  transition-duration: .5s;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__name {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  width: 166px;
  height: 16px;
  margin-bottom: 24px;
  display: flex;
}

.card__count-review {
  color: #060b35;
  align-items: center;
  height: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-count {
  justify-content: flex-end;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__price {
  color: #060b35;
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card:hover {
  transition-delay: .3s;
  transform: scale(1.2);
}

.card:hover .card__name {
  color: #34568b;
  transition-delay: .3s;
}

.card__buy-button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 164px;
  height: 38px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition-duration: .5s;
  display: flex;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  transition-duration: .5s;
  transition-delay: .3s;
  transition-timing-function: linear;
}

.card__buy-button:hover .card__buy-button__buy-button {
  color: #00acdc;
  background: #fff;
  transition-delay: .3s;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star--4 {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

